<template>
    <div>
        <div id="head">
         <img src="../../assets/back.png" style="width:0.3rem;" @click="ht()">
         <span>订单列表</span>
        </div>
    </div>
</template>
<script>
export default {
    name:'ord',
    data() {
        return {
            input:""
        }
    },
    methods: {
        ht(){
            this.$router.back()
        }
    },
}
</script>
<style scoped>
#head {
  position: relative;
  background-color: rgb(48, 144, 232);
  font-size: 0.16rem;
  color: white;
  padding:0.07rem;
}
#head span{
    font-size:0.2rem;
    /* font-weight:bold; */
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%)
}
</style>



